import React from "react";
import Cards from "./Cards";

const titles = [
  { name: "Houses", href: "", current: true },
  { name: "Public Building", href: "", current: false },
  { name: "Hotel & Resorts", href: "", current: false },
  { name: "Landscape", href: "", current: false },
  // { name: "CONTACT US", href: "#", current: false },
];

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

const Gallery = () => {
  return (
    <div className="text-gray-500 py-10">
      <h1 className="text-center text-xl lg:text-4xl font-[codec-thin] font-bold py-12 text-gray-400 uppercase">
        find your desired construction
        <br />
        you always dream to build
      </h1>
      <div className="text-center">
        {titles.map((item) => (
          <button
            key={item.name}
            // href={item.href}
            className={classNames(
              item.current
                ? "bg-gradient-to-r from-emerald-400  to-cyan-500 text-white "
                : "text-gray-400  hover:text-gray-900",
              "rounded-full px-8 tracking-widest py-3  text-[10px] font-bold"
            )}
            aria-current={item.current ? "page" : undefined}
          >
            {item.name}
          </button>
        ))}
      </div>
      <div className="flex items-center justify-center ">
        <Cards/>
      </div>
    </div>
  );
};

export default Gallery;
