import React from "react";
import Carousel from "./Carousel";

const CarouselSlider = () => {
  return (
    <div className="mb-8">
      <div className="grid">
        <div className="py-2 text-3xl uppercase lg:text-7xl lg:absolute lg:left-0  lg:py-20 lg:px-40 lg:z-[99]">
          <h1 className="font-[codec-thin] text-center lg:text-end">
            build
            <br /> your own
          </h1>
          <h1 className="font-extrabold text-gradient bg-gradient-to-r from-amber-600  to-cyan-700 text-transparent bg-clip-text text-center lg:text-start">
            building
          </h1>
        </div>
        <div>
          <Carousel autoSlide={true} autoSlideInterval={2000} />
        </div>
      </div>
    </div>
  );
};

export default CarouselSlider;
