import React from "react";
import { materials } from "./matirials";
import object from "../../Assets/decores/view-05.webp"

const objects = ["Walls", "Doors", "windows", "Floor", "Furniture"]
const MaterialPlate = () => {
  return (
    <div className="md:px-16">
      <div className="py-16 grid grid-cols-1 lg:grid-cols-2 place-items-center">
        <div className=" p-8">
          <h1 className="uppercase text-xl md:text-4xl font-[codec-thin] font-bold pb-8">
            Materials
          </h1>
          <div className="grid grid-cols-4 gap-5 ">
            {materials.map((item) => (
              <div>
                <div className="rounded-xl p-1 border-2 border-gray-400">
                  <button className="grid">
                    <img src={item.url} alt="" className="w-20 rounded-lg" />
                  </button>
                </div>
                <h1 className="text-center font-medium font-[montserrat]">
                  {item.title}
                </h1>
              </div>
            ))}
          </div>
        </div>
        <div className="p-8">
          <h1 className="uppercase text-xl md:text-4xl font-[codec-thin] font-bold pb-8">
            Objects
          </h1>
          <div className="flex h-80 w-full justify-center items-center overflow-hidden">
            <img src={object} className="h-full "/>
            
          </div>
          <div className="grid grid-cols-2 md:grid-cols-5 place-items-center pt-6 ">
            {objects.map((obj)=>(
                <button className={`px-4 py-1 font-bold rounded-full
                ${obj==="Walls"?"bg-cyan-400 text-cyan-50":""}
                `
                }>{obj}</button>
            ))}
            </div>
        </div>
      </div>
    </div>
  );
};

export default MaterialPlate;
