import React, { useState } from "react";
import { partners } from "./PartnersLogos";

const Partners = () => {
  return (
    <div className="px-4 py-16">
      <h1 className="text-center font-[codec-thin] font-bold text-xl md:text-4xl uppercase text-gray-500">
        Our Partners
      </h1>
      <div>
        <div className="flex  flex-wrap w-full justify-evenly py-8">
          {partners.map((item) => (
            <div className="flex p-5 ">
              <img src={item.url2} alt="" className="w-20 md:w-40" />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Partners;
