import React from "react";
import logo from "../../Assets/logo/logo-04.webp";
import { socialMedia } from "./socialMedia";
import { store } from "./store";

const quickLinks = [
  { name: "ABOUT US", href: "#", current: false },
  { name: "TERMS AND CONDITIONS", href: "#", current: false },
  { name: "HELP AND SUPPORT", href: "#", current: false },
  { name: "POLICY PRIVACY", href: "#", current: false },
  { name: "OUR PROJECTS", href: "#", current: false },
];
const designHome = [
  { name: "STYLES", href: "#", current: false },
  { name: "BUY YOUR FURNITURE", href: "#", current: false },
  { name: "DECORATE YOUR ROOMS", href: "#", current: false },
  { name: "ADD YOUR TOUCHES", href: "#", current: false },
  { name: "DESIGN TIPS", href: "#", current: false },
];
const achievements = [
  { name: "OUR WORKS", href: "#", current: false },
  { name: "BUY YOUR FURNITURE", href: "#", current: false },
  { name: "DECORATE YOUR ROOMS", href: "#", current: false },
  { name: "ADD YOUR TOUCHES", href: "#", current: false },
  { name: "DESIGN TIPS", href: "#", current: false },
];

const Footer = () => {
  return (
    <div className="pt-10 px-10 text-center lg:text-start text-gray-50 bg-gray-950  font-[codec-thin] font-bold">
      <div className="grid grid-cols-1 md:grid-cols-4">
        <div className="grid px-8 items-center justify-center md:justify-start">
          <img src={logo} alt="" className="w-40" />
        </div>
        <div className="mx-4 py-6">
          <div>
            <h1 className="text-lg uppercase">Quick Links</h1>
            <div className="grid grid-cols-1 md:grid-cols-2 space-y-2 py-6 text-gray-400 lg:border-r lg:border-gray-700">
              {quickLinks.map((item) => (
                <a
                  href={item.href}
                  className="text-[11px] whitespace-nowrap hover:text-gray-100 duration-500"
                >
                  {item.name}
                </a>
              ))}
            </div>
          </div>
        </div>
        <div className="mx-4 py-6">
          <div>
            <h1 className="text-lg uppercase">Design your new home</h1>
            <div className="grid grid-cols-1 md:grid-cols-2 space-y-2 py-6 text-gray-400 lg:border-r lg:border-gray-700">
              {designHome.map((item) => (
                <a
                  href={item.href}
                  className="text-[11px] whitespace-nowrap hover:text-gray-100 duration-500"
                >
                  {item.name}
                </a>
              ))}
            </div>
          </div>
        </div>
        <div className="mx-4 py-6">
          <div>
            <h1 className="text-lg uppercase">our achievements</h1>
            <div className="grid grid-cols-1 md:grid-cols-2 space-y-2 py-6 text-gray-400 lg:border-r lg:border-gray-700">
              {achievements.map((item) => (
                <a
                  href={item.href}
                  className="text-[11px] whitespace-nowrap hover:text-gray-100 duration-500"
                >
                  {item.name}
                </a>
              ))}
            </div>
          </div>
        </div>
      </div>
      {/* <hr className="mx-16 lg:mx-72 mt-4" /> */}
      <div className="py-2  border-t border-gray-700 grid grid-cols-1 md:grid-cols-3 w-full justify-between items-center">
        <div className="flex p-4 md:px-0 justify-center md:justify-start gab-6">
          {store.map((item) => (
            <a href="#" className="">
              <img src={item.icon} className="h-8"></img>
            </a>
          ))}
        </div>
        <div className="order-3  md:col-span-1 py-2 flex justify-center">
            <h1 className="uppercase text-xs text-gray-400">Copyright <span className="font-[montserrat]">2023</span> Kuwaitnet | All Rights Reserved</h1>
        </div>
        <div className="flex md:order-3 p-4 md:px-0 justify-center md:justify-end gap-2">
          {socialMedia.map((item) => (
            <a href="#" className="">
              <img src={item.icon} className="w-6"></img>
            </a>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Footer;
