import React from "react";
import card1 from "../../Assets/gallery/gallery-01.webp";
import card2 from "../../Assets/gallery/gallery-02.webp";
import card3 from "../../Assets/gallery/gallery-03.webp";
import card4 from "../../Assets/gallery/gallery-04.webp";
import card5 from "../../Assets/gallery/gallery-05.webp";

const cards = [card1, card2, card3, card4, card5];

const Cards = () => {
  return (
    <div className=" grid items-center justify-center sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-5 p-5 ">
      {cards.map((slide) => (
        <div className="shadow p-5 h-full justify-between flex flex-col space-y-5">
          <div className="relative grid  justify-center items-center inline-block h-60 overflow-hidden">
            <img
              src={slide}
              alt=""
              className="absolute "
            />
            
          </div>
          <div>
          <h1 className="text-xl font-bold text-gray-900">Image Title</h1>
          <p className="">Lorem ipsum dolor sit amet consectetur adipisicing elit. Architecto autem provident dolore dignissimos quasi.</p>
          </div>
        </div>
      ))}
    </div>
  );
};

export default Cards;
