import React from 'react'
import {projects} from "./projectItems"

const Projects = () => {
  return (
    <div className='px-4 bg-gray-100 py-16'>
        <h1 className='text-center font-[codec-thin] font-bold text-xl md:text-4xl uppercase text-gray-500'>Our Projects</h1>
        <div>
            <div className='grid grid-cols-1 md:grid-cols-3 lg:grid-cols-4 gap-8 py-8'>
                {projects.map((item)=>(
                    <a href='#' className='shadow-lg'>
                        <img src={item.url} alt="" />
                        <p className='p-4 md:font-bold text-gray-500'>{item.text}</p>
                    </a>
                ))}
            </div>
        </div>
    </div>
  )
}


export default Projects