import React from "react";
import MaterialPlate from "./MaterialPlate";
import { decors } from "./Decors";

const items = ["one", "two", "three", "four"];

const ProductsItems = () => {
  return (
    <div className="md:py-4 ">
    <div className="grid grid-cols-4 place-items-center">
      {decors.map((item) => (
        <div className="flex flex-col w-full place-items-center">
          <div className="p-5 w-full bg-gray-100 grid justify-center items-center">
            <img src={item.url} alt="" className="w-full md:p-10"/>
          </div>
          <button
            className={`text-sm md:text-lg whitespace-nowrap my-4 px-4 py-1 rounded-full  font-[codec-thin] font-bold
            ${item.title === "Living Room" ? `bg-cyan-400 text-cyan-50` : `bg-white`}
          `}
          >
            {item.title}
          </button>
        </div>
      ))}
      </div>
      <MaterialPlate/>
    </div>
  );
};

export default ProductsItems;
