import { Disclosure} from "@headlessui/react";
import { Bars3Icon, XMarkIcon } from "@heroicons/react/24/outline";
import logo from "../Assets/logo/logo-02.webp"

const navigation = [
  { name: "Home", href: "#", current: true },
  { name: "ABOUT US", href: "#", current: false },
  { name: "HOW IT WORKS", href: "#", current: false },
  { name: "PROJECTS", href: "#", current: false },
  { name: "CONTACT US", href: "#", current: false },
];
const registing = [
//   { name: "Dashboard", href: "#", current: true },
  { name: "LOG IN", href: "#", current: false },
  { name: "SIGN UP", href: "#", current: false },
];
const language = [
//   { name: "Dashboard", href: "#", current: true },
  { name: "العربية", href: "#", current: false },
];

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

const Navbar = () => {
  return (
    <Disclosure as="nav" className="bg-[#fefefe]">
      {({ open }) => (
        <>
          <div className="px-2 lg:px-6 lg:px-8">
            <div className=" relative flex h-16 items-center justify-between">
              <div className="absolute z-[99] inset-y-0 left-0 flex items-center lg:hidden">
                {/* Mobile menu button*/}
                <Disclosure.Button className="relative inline-flex items-center justify-center rounded-md p-2 text-gray-900  hover:text-gray-500 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white">
                  <span className="absolute -inset-0.5" />
                  <span className="sr-only ">Open main menu</span>
                  {open ? (
                    <XMarkIcon className="block h-6 w-6" aria-hidden="true" />
                  ) : (
                    <Bars3Icon className="block h-6 w-6" aria-hidden="true" />
                  )}
                </Disclosure.Button>
              </div>
              
              <div className="relative flex flex-1 items-center justify-center">
              <div className="flex flex-shrink-0  items-center">
                  <img
                    className="h-8 lg:h-12 w-auto "
                    src={logo}
                    alt="Your Company"
                  />
                </div>
                <div className="flex border-t lg:ms-24 border-black lg:w-full lg:justify-end">
                <div className="hidden px-6 lg:ml-6 lg:block border-r border-black">
                  <div className=" flex space-x-3 w-full justify-evenly items-center">
                    {navigation.map((item) => (
                      <a
                        key={item.name}
                        href={item.href}
                        className={classNames(
                          item.current
                            ? "bg-gray-900 text-white "
                            : "text-gray-400  hover:text-gray-900",
                          "rounded-md mt-1 px-4 tracking-widest py-2 text-[10px] font-normal"
                        )}
                        aria-current={item.current ? "page" : undefined}
                      >
                        {item.name}
                      </a>
                    ))}
                  </div>
                </div>

                <div className="hidden px-6 lg:ml-6 lg:block border-r border-black">
                  <div className="flex space-x-3 w-full justify-evenly">
                    {registing.map((item) => (
                      <a
                        key={item.name}
                        href={item.href}
                        className={classNames(
                          item.current
                            ? "bg-gray-900 text-white"
                            : "text-gray-400  hover:text-gray-900",
                          "rounded-md px-4 tracking-widest py-2 text-[10px] font-normal"
                        )}
                        aria-current={item.current ? "page" : undefined}
                      >
                        {item.name}
                      </a>
                    ))}
                  </div>
                </div>


                <div className="hidden ps-6 pe-0 lg:ml-6 lg:block">
                  <div className="flex space-x-3 w-full justify-evenly">
                    {language.map((item) => (
                      <a
                        key={item.name}
                        href={item.href}
                        className={classNames(
                          item.current
                            ? "bg-gray-900 text-white"
                            : "text-gray-400  hover:text-gray-900",
                          "rounded-md px-4 tracking-widest py-2 lg:text-[10px] font-normal"
                        )}
                        aria-current={item.current ? "page" : undefined}
                      >
                        {item.name}
                      </a>
                    ))}
                  </div>
                </div>
                </div>
              </div>
            </div>
          </div>

          <Disclosure.Panel className="lg:hidden absolute duration-500 bg-gray-50 h-screen w-3/5 z-[99]">
            <div className="space-y-1 px-2 pb-3 pt-2">
              {navigation.map((item) => (
                <Disclosure.Button
                  key={item.name}
                  as="a"
                  href={item.href}
                  className={classNames(
                    item.current
                      ? "bg-gray-900 text-white"
                      : "text-gray-900  hover:text-gray-500",
                    "block rounded-md px-3 py-2 text-base font-medium"
                  )}
                  aria-current={item.current ? "page" : undefined}
                >
                  {item.name}
                </Disclosure.Button>
              ))}
              {registing.map((item) => (
                      <a
                        key={item.name}
                        href={item.href}
                        className={classNames(
                    item.current
                      ? "bg-gray-900 text-white"
                      : "text-gray-900  hover:text-gray-500",
                    "block rounded-md px-3 py-2 text-base font-medium"
                  )}
                        aria-current={item.current ? "page" : undefined}
                      >
                        {item.name}
                      </a>
                    ))}
            </div>
          </Disclosure.Panel>
        </>
      )}
    </Disclosure>
  );
}

export default Navbar;


